
import { Routes } from "@angular/router";

import { NotFoundComponent } from "./not-found/not-found.component";
import { ErrorComponent } from "./error/error.component";
import { SigninComponent } from "./signin/signin.component";
import { OtpComponent } from "./otp/otp.component";
import { LoginComponent } from "./login/login.component";
import { ScannerComponent } from "../others/scanner/scanner.component";
import { FingerprintComponent } from "./fingerprint/fingerprint.component";
import { WalkInCustomerComponent } from "./walk-in-customer/walk-in-customer.component";
import { AppointmentIdComponent } from "../others/appointment-id/appointment-id.component";
import { BranchLoginComponent } from "./branch-login/branch-login.component";
import { BranchLogoutComponent } from './branch-logout/branch-logout.component';

export const SessionsRoutes: Routes = [
  {
    path: "",
    children: [
      {
        // path: "signin",
        // component: SigninComponent,
        // data: { title: "Signin" }
        path: 'login',
        component: LoginComponent,
        data: { title: 'Login', breadcrumb: 'LOGIN' }
      },
      {
        path: 'otp',
        component: OtpComponent,
        data: { title: 'otp', breadcrumb: 'OTP' }
      },
      {
        path: 'walkInCustomer',
        component: WalkInCustomerComponent,
        data: { title: 'otp', breadcrumb: 'WALKIN' }
      },
      {
        path: 'appointment-id',
        component: AppointmentIdComponent ,
        data: { title: 'otp', breadcrumb: 'WALKIN' }
      },
      {
        path: "404",
        component: NotFoundComponent,
        data: { title: "Not Found" }
      },
      {
        path: "error",
        component: ErrorComponent,
        data: { title: "Error" }
      },
      {
        path: "scanner",
        component: ScannerComponent,
        // data: { title: "Error" }
      },
      {
        path: "scanFinger",
        component: FingerprintComponent,
        // data: { title: "Capture Finger Print" }
      },
      {
        path: "branch-login",
        component: BranchLoginComponent,
      },
      {
        path: "branch-logout",
        component: BranchLogoutComponent,
      },

    ]
  }
];
